/*
  About: This file is used to create the cursors for the oscilloscope. This is done
  with the use of PaperJS. How it all works is when the hit box is moved the cursor 
  (the circle and dashed line) will also move. Based of the position of dashed line,
  the position value will be updated and the difference will be taken to display to 
  the user. The values that are exported from this file are just the difference 
  between the time and voltage cursors. All the math is done with in wave.js.
*/

import Paper from "paper";

const clamp = (num, min, max) => Math.min(Math.max(num, min), max);

let cursorFlag = false;

export const setCursorFlag = (val) => cursorFlag = val;

const drawCursors = (onUpdate) => {
  let cursorLayer = new Paper.Layer();

  let timePos1 = .25;
  let timePos2 = .75;
  let voltPos1 = .25;
  let voltPos2 = .75;

  let timeCursorDiff = Math.abs(timePos1 - timePos2);
  let voltCursorDiff = Math.abs(voltPos1 - voltPos2);

  //Create cursor marks for demo
  var timeCursorLine1 = new Paper.Path({
    segments: [[Paper.view.bounds.width * timePos1, 0], [Paper.view.bounds.width * timePos1, Paper.view.bounds.height]],
    selected: false
  });
  
  timeCursorLine1.strokeColor = 'Black';
  timeCursorLine1.strokeWidth = 3;
  timeCursorLine1.strokeCap = 'round';
  timeCursorLine1.dashArray = [10, 12];


  var timeCursor1 = new Paper.Path.Circle({
    center: Paper.view.center,
    
    radius: 15,
    fillColor: 'red'
  });

  var timeCursorLine2 = new Paper.Path({
    segments: [[Paper.view.bounds.width*timePos2, 0], [Paper.view.bounds.width*timePos2, Paper.view.bounds.height]],
    selected: false
  });
  
  timeCursorLine2.strokeColor = 'Black';
  timeCursorLine2.strokeWidth = 3;
  timeCursorLine2.strokeCap = 'round';
  timeCursorLine2.dashArray = [10, 12];

  var timeCursor2 = new Paper.Path.Circle({
    center: Paper.view.center,
    radius: 15,
    fillColor: 'red'
  });

  var voltCursorLine1 = new Paper.Path({
    segments: [[0, Paper.view.bounds.height *(1-voltPos1)], [Paper.view.bounds.width, Paper.view.bounds.height * (1-voltPos1)]],
    selected: false
  });
  
  voltCursorLine1.strokeColor = 'Black';
  voltCursorLine1.strokeWidth = 3;
  voltCursorLine1.strokeCap = 'round';
  voltCursorLine1.dashArray = [10, 12];


  var voltCursor1 = new Paper.Path.Circle({
    center: Paper.view.center,
    radius: 15,
    fillColor: 'red'
  });

  var voltCursorLine2 = new Paper.Path({
    segments: [[0, Paper.view.bounds.height*(1-voltPos2)], [Paper.view.bounds.width, Paper.view.bounds.height*(1-voltPos2)]],
    selected: false
  });
  
  voltCursorLine2.strokeColor = 'Black';
  voltCursorLine2.strokeWidth = 3;
  voltCursorLine2.strokeCap = 'round';
  voltCursorLine2.dashArray = [10, 12];
  
  var voltCursor2 = new Paper.Path.Circle({
    center: Paper.view.center,
    radius: 15,
    fillColor: 'red'
  });

  timeCursor1.position.x = Paper.view.bounds.width * timePos1;
  timeCursor1.position.y = Paper.view.bounds.height;

  voltCursor1.position.x = Paper.view.bounds.width;
  voltCursor1.position.y = Paper.view.bounds.height * (1-voltPos1);

  timeCursor2.position.x = Paper.view.bounds.width * timePos2;
  timeCursor2.position.y = Paper.view.bounds.height;

  voltCursor2.position.x = Paper.view.bounds.width;
  voltCursor2.position.y = Paper.view.bounds.height * (1-voltPos2);
  
  timeCursor1.visible = cursorFlag;
  timeCursor2.visible = cursorFlag;
  voltCursor1.visible = cursorFlag;
  voltCursor2.visible = cursorFlag;
  timeCursorLine1.visible = cursorFlag;
  timeCursorLine2.visible = cursorFlag;
  voltCursorLine1.visible = cursorFlag;
  voltCursorLine2.visible = cursorFlag;

  onUpdate(timeCursorDiff, voltCursorDiff);


  // Move cursor using touch
  var hitOptions = {
    stroke: true,
    fill: true,
    tolerance: 50,
  }

  var timeCursor1Active;
  var voltCursor1Active;
  var timeCursor2Active;
  var voltCursor2Active;

  Paper.view.on('mousedown', function(event) {
    var timeHitResult1 = timeCursor1.hitTest(event.point, hitOptions);
    timeCursor1Active = timeHitResult1 && timeHitResult1.item;

    var voltHitResult1 = voltCursor1.hitTest(event.point, hitOptions);
    voltCursor1Active = voltHitResult1 && voltHitResult1.item;

    var timeHitResult2 = timeCursor2.hitTest(event.point, hitOptions);
    timeCursor2Active = timeHitResult2 && timeHitResult2.item;

    var voltHitResult2 = voltCursor2.hitTest(event.point, hitOptions);
    voltCursor2Active = voltHitResult2 && voltHitResult2.item;

  });

  Paper.view.on('mousedrag', function(event) {
    if (timeCursor1Active) {
      timePos1 = clamp(event.point.x / Paper.view.bounds.width, 0.0, 1.0);
      timeCursor1Active.position.x = Paper.view.bounds.width * timePos1;
      timeCursorLine1.position.x = Paper.view.bounds.width * timePos1;
    } else if (voltCursor1Active) {
      voltPos1 = clamp((1 - (event.point.y / Paper.view.bounds.height)), 0.0, 1.0);
      voltCursor1Active.position.y = Paper.view.bounds.height * (1 - voltPos1);
      voltCursorLine1.position.y = Paper.view.bounds.height * (1 - voltPos1);
      console.log(Paper.view.bounds.height);
    } else if (timeCursor2Active) {
      timePos2 = clamp(event.point.x / Paper.view.bounds.width, 0.0, 1.0);
      timeCursor2Active.position.x = Paper.view.bounds.width * timePos2;
      timeCursorLine2.position.x = Paper.view.bounds.width * timePos2;
    } else if (voltCursor2Active) {
      voltPos2 = clamp((1 - (event.point.y / Paper.view.bounds.height)), 0.0, 1.0);
      voltCursor2Active.position.y = Paper.view.bounds.height * (1 - voltPos2);
      voltCursorLine2.position.y = Paper.view.bounds.height * (1 - voltPos2);
    }

    timeCursorDiff = Math.abs(timePos1 - timePos2);
    voltCursorDiff = Math.abs(voltPos1 - voltPos2);

    onUpdate(timeCursorDiff, voltCursorDiff);

  });

  Paper.view.on('mouseup', function (event) {
    timeCursor1Active = null;
    voltCursor1Active = null;
    timeCursor2Active = null;
    voltCursor2Active = null;
  });

  
  Paper.view.on('resize', function (event) {
    cursorLayer.activate();
    timeCursorLine1.remove();
    timeCursorLine2.remove();
    voltCursorLine1.remove();
    voltCursorLine2.remove();

    timeCursor1.remove();
    timeCursor2.remove();
    voltCursor1.remove();
    voltCursor2.remove();

    timeCursorLine1 = new Paper.Path({
      segments: [[timePos1 * Paper.view.bounds.width, 0], [timePos1 * Paper.view.bounds.width, Paper.view.bounds.height]],
      selected: false
    });
    
    timeCursorLine1.strokeColor = 'Black';
    timeCursorLine1.strokeWidth = 3;
    timeCursorLine1.strokeCap = 'round';
    timeCursorLine1.dashArray = [10, 12];
  
  
    timeCursor1 = new Paper.Path.Circle({
      center: Paper.view.center,
      radius: 15,
      fillColor: 'red'
    });
  
    timeCursorLine2 = new Paper.Path({
      segments: [[timePos2 * Paper.view.bounds.width, 0], [timePos2 * Paper.view.bounds.width, Paper.view.bounds.height]],
      selected: false
    });
    
    timeCursorLine2.strokeColor = 'Black';
    timeCursorLine2.strokeWidth = 3;
    timeCursorLine2.strokeCap = 'round';
    timeCursorLine2.dashArray = [10, 12];
  
    timeCursor2 = new Paper.Path.Circle({
      center: Paper.view.center,
      radius: 15,
      fillColor: 'red'
    });
  
    voltCursorLine1 = new Paper.Path({
      segments: [[0, (1-voltPos1) * Paper.view.bounds.height], [Paper.view.bounds.width, (1-voltPos1) * Paper.view.bounds.height]],
      selected: false
    });
    
    voltCursorLine1.strokeColor = 'Black';
    voltCursorLine1.strokeWidth = 3;
    voltCursorLine1.strokeCap = 'round';
    voltCursorLine1.dashArray = [10, 12];
  
  
    voltCursor1 = new Paper.Path.Circle({
      center: Paper.view.center,
      radius: 15,
      fillColor: 'red'
    });
  
    voltCursorLine2 = new Paper.Path({
      segments: [[0, (1-voltPos2) * Paper.view.bounds.height], [Paper.view.bounds.width, (1-voltPos2) * Paper.view.bounds.height]],
      selected: false
    });
    
    voltCursorLine2.strokeColor = 'Black';
    voltCursorLine2.strokeWidth = 3;
    voltCursorLine2.strokeCap = 'round';
    voltCursorLine2.dashArray = [10, 12];
    
    voltCursor2 = new Paper.Path.Circle({
      center: Paper.view.center,
      radius: 15,
      fillColor: 'red'
    });

    timeCursor1.position.x = Paper.view.bounds.width * timePos1;
    timeCursor1.position.y = Paper.view.bounds.height;
  
    voltCursor1.position.x = Paper.view.bounds.width;
    voltCursor1.position.y = Paper.view.bounds.height * (1-voltPos1);
  
    timeCursor2.position.x = Paper.view.bounds.width * timePos2;
    timeCursor2.position.y = Paper.view.bounds.height;
  
    voltCursor2.position.x = Paper.view.bounds.width;
    voltCursor2.position.y = Paper.view.bounds.height * (1-voltPos2);

    timeCursor1.visible = cursorFlag;
    timeCursor2.visible = cursorFlag;
    voltCursor1.visible = cursorFlag;
    voltCursor2.visible = cursorFlag;
    timeCursorLine1.visible = cursorFlag;
    timeCursorLine2.visible = cursorFlag;
    voltCursorLine1.visible = cursorFlag;
    voltCursorLine2.visible = cursorFlag;
  });

  Paper.view.on('frame', function (event) {
    timeCursor1.visible = cursorFlag;
    timeCursor2.visible = cursorFlag;
    voltCursor1.visible = cursorFlag;
    voltCursor2.visible = cursorFlag;
    timeCursorLine1.visible = cursorFlag;
    timeCursorLine2.visible = cursorFlag;
    voltCursorLine1.visible = cursorFlag;
    voltCursorLine2.visible = cursorFlag;
  });

}

export default drawCursors;