/*
  About: This file is used to create the triggers for the oscilloscope. This is done
  with the use of PaperJS. How it all works is there is a hit box is moved, the the 
  trigger (the triangle and the dashed line) will move. Based of the position of 
  triangle is at, this value is then passed to wave.js to be used to move the buffer.
*/

import Paper from "paper";

const clamp = (num, min, max) => Math.min(Math.max(num, min), max);

const HORIZONTAL = 0;
const VERTICAL = 1;

class Trigger {
  constructor(axis) {
    let numOfWBoxes = 10;
    let numOfHBoxes = 8;

    let length = 10;

    this.axis = axis;

    this.basePath = new Paper.Path({
      segments: [[0, 0], [length, length], [length*2, 0]],
      fillColor: 'black',
      selected: false,
      closed: true,
      rotation: this.axis === HORIZONTAL ? 0 : -90,
    });

    this.selecting = false;
    this.pos = 0.5;

    if(this.axis === HORIZONTAL) {
      this.basePath.position.x = Paper.view.bounds.width * this.pos;
      this.basePath.position.y = length/2;
    } else {
      this.basePath.position.x = length/2;
      this.basePath.position.y = Paper.view.bounds.height * (1 - this.pos);
    }
  }

  mousedown(event) {
    const hitOptions = {
      stroke: true,
      fill: true,
      tolerance: 50,
    }
    
    const hitDetected = this.basePath.hitTest(event.point, hitOptions);
    this.selecting = hitDetected && hitDetected.item;
  }

  mousedrag(event) {
    if(this.selecting) {
      if(this.axis === HORIZONTAL) {
        this.pos = clamp(event.point.x / Paper.view.bounds.width, 0.01, 0.99);
        this.basePath.position.x = Paper.view.bounds.width * this.pos;
      } else {
        this.pos = 1 - clamp(event.point.y / Paper.view.bounds.height, 0.01, 0.99);
        this.basePath.position.y = Paper.view.bounds.height * (1 - this.pos);
      }

      return this.pos;
    }
  }

  mouseup(event) {
    this.selecting = null;
  }

  resize(event) {
    if(this.axis === HORIZONTAL) {
      this.basePath.position.x = Paper.view.bounds.width * this.pos;
    } else {
      this.basePath.position.y = Paper.view.bounds.height * (1 - this.pos);
    }
  }
}

const drawTriggers = (onUpdate) => {
  let triggerLayer = new Paper.Layer();
  let timePos = 0.5;
  let voltPos = 0.5;
  
  let timeDiff = timePos - 0.5;
  let voltDiff = voltPos - 0.5;

  //Create trigger marks for demo
  let timeTrigger = new Trigger(HORIZONTAL);
  var voltTrigger = new Trigger(VERTICAL);

  Paper.view.onMouseDown = function (event) {
    timeTrigger.mousedown(event);
    voltTrigger.mousedown(event);
  }

  Paper.view.onMouseDrag = function (event) {
    timePos = timeTrigger.mousedrag(event) ?? timePos;
    voltPos = voltTrigger.mousedrag(event) ?? voltPos;

    timeDiff = timePos;
    voltDiff = 2*voltPos - 1;

    onUpdate(timeDiff, voltDiff);
  }

  Paper.view.onMouseUp = function (event) {
    timeTrigger.mouseup(event);
    voltTrigger.mouseup(event);
  };

  Paper.view.on('resize', function (event) {
    triggerLayer.activate();

    timeTrigger.resize(event);
    voltTrigger.resize(event);
  });
}

export default drawTriggers;

